import {Component, OnInit, ViewEncapsulation, ChangeDetectionStrategy, Inject} from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material';
import {CrupdatePlanModalData} from '../../admin/billing/plans/crupdate-plan-modal/crupdate-plan-modal.component';
import {FormControl} from '@angular/forms';
import {catchError, debounceTime, distinctUntilChanged, finalize, switchMap} from 'rxjs/operators';
import {BehaviorSubject, Observable, of} from 'rxjs';
import {Users} from '../users.service';
import {User} from '../../core/types/models/User';

@Component({
    selector: 'find-users-modal',
    templateUrl: './find-users-modal.component.html',
    styleUrls: ['./find-users-modal.component.scss'],
    encapsulation: ViewEncapsulation.None,
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class FindUsersModalComponent implements OnInit {
    public searchFormControl = new FormControl();
    public loading$ = new BehaviorSubject(false);
    public users$ = new BehaviorSubject<User[]>([]);

    constructor(
        private dialogRef: MatDialogRef<FindUsersModalComponent>,
        @Inject(MAT_DIALOG_DATA) public data: CrupdatePlanModalData,
        private users: Users,
    ) {}

    ngOnInit() {
        this.searchFormControl.valueChanges
            .pipe(
                debounceTime(250),
                distinctUntilChanged(),
                switchMap(query => this.searchUsers(query)),
                catchError(() => of([])),
            ).subscribe(users => {
                this.users$.next(users);
                this.loading$.next(false);
            });
    }

    private searchUsers(query: string): Observable<User[]> {
        this.loading$.next(true);
        if ( ! query) {
            return of([]);
        }
        return this.users.getAll({query});
    }

    public close(user?: User) {
        this.dialogRef.close(user);
    }
}
